

.quote {
    font-family: 'Amatic SC', cursive;
    font-size: 2.5em;
    line-height: 110%;
    font-weight: 600;
    color: #F2786F
}

.author {
    float: right;
    color: #F2786F;
}
.btn-twitter {
    background: #00ACEE;
    border-radius: 5;
    color: #fff;
    border-width: 1;
    border-style: solid;
    border-color: #0075a2;
    margin-left: 10;
}
.quote-box {
    width: 500px;
    margin: auto;
    border-radius: 8;
    margin-top: 100px;
    padding: 20px;
    background-color: #fff;
}

.quote-container {
    padding: 10px;
    width: 500px;
    margin: auto;
}
